<template>
  <div class="Fc_message">
    <span>{{tips}}</span>
  </div>
</template>

<script type="text/javascript">
export default {
  name: "FcMessage",
  data() {
    return {
      tips: "",
      duration: 1000
    };
  },
  mounted() {
    this.$nextTick(function() {
      this.tips = this.$options.tips;
      this.duration = this.$options.duration;
      setTimeout(() => {
        this.$el.remove();
        this.$options.fn && this.$options.fn();
      }, this.duration);
    });
  }
};
</script>

<style lang="less">
.Fc_message {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 14px;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  z-index: 9999;
  span {
    background: rgba(0, 0, 0, 0.75);
    color: #fff;
    padding: 0.6em 3em;
    border-radius: 4px;
    -webkit-animation: 0.2s down linear;
    animation: 0.2s down linear;
    white-space: nowrap;
  }
}
@keyframes down {
  0% {
    opacity: 0.2;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
  }
}
</style>