            <blockquote>
                <p>tips: <code>loading</code>, <code>$loading</code>, </p>
            </blockquote>
            <ul>
                <li>
                    <code>string</code><code>loading</code>, <code>loading</code>, 
                </li>
                <li>
                    <code>close</code>
                </li>
                <li>
                    ? : <code>xumeng0611@gmail.com</code>
                </li>
            </ul>
            <h3></h3>
            <p>1.<code>loading</code></p>
            <xui-button type="default" @click="loading1"></xui-button>
            <codes :codes="loading_1"></codes>
            <p>2.<code>loading</code></p>
            <blockquote>
                <p>tips: <code>position:relative;</code></p>
