        </div>
    </div>
</template>

<script type="text/javascript">
import Codes from '@/assets/codes.vue';

export default {
    name: 'xui-loading',
    data(){
        return{
            isDisabled: false,
            params: `Vue.use(XuiLoading);
let golbalLoading = this.$loading();`,
            params1: `Vue.prototype.$showLoading = XuiLoading.LoadingService
let partLoading = this.$showLoading(string);`,
            loading_1: `let golbalLoading = this.$loading();
setTimeout(()=>{
    golbalLoading.close();
},2000);
`,
            loading_2: `let partLoading = this.$showLoading('#test');
setTimeout(()=>{
    partLoading.close();
},2000);
`,
        }
    },
    mounted(){
        if ('ontouchstart' in window) {
            let tar = document.getElementById('app');
            tar.querySelector('.test').classList.add('folded');
            tar.querySelector('.main').classList.add('folded');
        }
    },
    methods:{
        loading1(){
            let golbalLoading = this.$loading();
            setTimeout(()=>{
                golbalLoading.close();
            },2000);
        },
        loading2(){
            this.isDisabled = true;
            let partLoading = this.$showLoading('#test');
            setTimeout(()=>{
                partLoading.close();
                this.isDisabled = false;
            },2000);
        }
